.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40vh;
  background-color: #f0f0f0;
  color: black; 
  text-align: center;
  position: relative;
  overflow: hidden;
}

.hero .threeD-container {
  perspective: 1000px;
  width: 600px;
  height: 300px;
}

.hero .threeD-box {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transform: rotateY(0deg);
  transition: transform 1s;
}

.hero .threeD-container:hover .threeD-box {
  transform: rotateY(180deg);
}

.hero .face {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  padding: 20px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 10px;
}

.hero .front {
  background: #ddd;
}

.hero .back {
  background: #4682b4;
  transform: rotateY(180deg);
}
