.about {
    background-color: #fff;
    text-align: center;
    padding: 60px 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
  }
  
  .about h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .about p {
    font-size: 1.2rem;
    margin-bottom: 20px;
  }
  
  .about a {
    color: #007bff;
    text-decoration: none;
    font-weight: bold;
  }
  
  .about a:hover {
    text-decoration: underline;
  }
  