/* src/ThreeD.css */

.threeD-container {
    perspective: 1000px;
    width: 300px;
    height: 300px;
    margin: 0 auto;
    margin-top: 50px;
  }
  
  .threeD-box {
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transform: rotateY(0deg);
    transition: transform 1s;
  }
  
  .threeD-container:hover .threeD-box {
    transform: rotateY(180deg);
  }
  
  .face {
    position: absolute;
    width: 300px;
    height: 300px;
    backface-visibility: hidden;
  }
  
  .front {
    background: #ff6347;
  }
  
  .back {
    background: #4682b4;
    transform: rotateY(180deg);
  }
  