body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f5f5f5;
  color: #333;
}

a {
  color: inherit;
  text-decoration: none;
}

h2 {
  margin-top: 0;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  flex: 1;
}

section {
  padding: 60px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

h1, h2, h3 {
  font-family: 'Helvetica', sans-serif;
  font-weight: bold;
}

html {
  scroll-behavior: smooth;
}
